/*----------------------------------------------------*/
.wrapper[data-bg="login"]{
  min-height: 100vh;
  background: url("../images/login-bg.jpg") no-repeat left bottom #E0F2FE;
  background-size: 100%;
  @include pad{
    background-size: 1000px;
  }
  @include mobile-lg{
    background-size: 700px;
  }
}

.login{
  display: flex;
  min-height: 100vh;
  align-items: center;
  @include mobile-md{
    padding-top: 80px;
  }
  &-logo{
    width: 360px;
    position: absolute;
    left: 56px;
    top: 40px;
    @include PC-lg{
      width: 300px;
      left: 24px;
      top: 24px;
    }
    @include PC{
      width: 280px;
    }
    @include mobile-lg{
      width: 250px;
    }
    @include mobile-md{
      width: 220px;
    }
  }
  &-title{
    font-size: $font-xxxxl;
    font-weight: $font-xheavy;
    color: $gray800;
    font-family: "Noto Serif TC", "Microsoft JhengHei", "sans-serif";
    text-align: center;
    @include PC-lg{
      font-size: $font-xxxl;
    }
    @include pad{
      font-size: $font-xxl;
    }
    @include mobile-lg{
      font-size: $font-xl;
    }
    span{
      font-size: $font-xl;
      font-weight: $font-light;
       @include PC-lg{
      font-size: $font-lg;
    }
    @include mobile-lg{
      font-size: $font-md;
    }
    }
  }
  &-form{
    width: 90%;
    max-width: 580px;
    margin-bottom: 80px;
    @include PC-lg{
      max-width: 500px;
    }
    @include pad{
      max-width: 440px;
    }
  }
  &-btn{
    border-radius: 15px !important;
    height: 65px;
    font-size: $font-lg;
    @include PC-lg{
      height: auto;
    }
    @include mobile-lg{
      font-size: $font-md;
    }
  }
  input:not([type="checkbox"]){
    border-radius: 15px;
    background: $white;
    padding-left: 20px;
    height: 55px;
    font-size: $font-smd;
    @include PC-md{
      height: auto;
    }
    &::placeholder{
      font-size: $font-smd;
      @include mobile-md{
        font-size: $font-sm;
      }
    }
    &[data-con="account"]{
      background: url("../images/ic-account.svg") no-repeat right center $white;
      padding-right: 55px;
      border-width: 2px;
      @include mobile-lg{
        background-size: 40px;
      }
    }
    &[data-con="password"]{
      background: url("../images/ic-password.svg") no-repeat right center $white;
      padding-right: 55px;
      border-width: 2px;
      @include mobile-lg{
        background-size: 40px;
      }
    }
  }
  form>div{
    margin-bottom: 40px;
    @include PC-lg{
      margin-bottom: 24px;
    }
  }
}
